<template>
<div>
    <!--农场-->
    <div class="panel panel-info">
        <div class="panel-body">
            <div>实时监测</div>
            <div>
                <div class="panel panel-default">
                    <div class="panel-heading">
                        <div>
                            <h4>
                                <span v-for="(item,index) in typeCheckData" :key="index" v-html="item.name+'&nbsp;&nbsp;&gt;&nbsp;&nbsp;'" v-show="index<typenum"></span>
                                <span v-html="initData[typenum].title"></span>
                                <a v-if="typenum!=0" class="btn btn-link pull-right" @click="loginItems(-1)">返回上一级</a>
                            </h4>
                        </div>
                    </div>
                    <div class="panel-body">
                        <!--选择位置-->
                        <div v-show="!sensorInfVisible">
                            <div v-if="items.length>0">
                                <div class="col-sm-2 col-md-1 col-lg-1" v-for="(item,index) in items" :key="index">
                                    <button class="btn btn-default" @click="loginItems(1,item)"  v-html="item.name"></button>
                                </div>
                            </div>
                            <div v-show="items.length<=0" class="col-sm-12 txt-center cursor-pointer logindata-error">
                                未查找到数据，点击请重试
                            </div>
                        </div>
                        <!--传感器详细信息-->
                        <div v-show="sensorInfVisible">
                            <!--当前信息-->
                            <div v-show="NowAndHistory">
                                <div>
                                    <a class="btn btn-link pull-right" @click="NowAndHistory=false">查看历史记录</a>
                                </div>
                                <div id="myChartNow" :style="{width: '600px', height: '500px',margin:'auto'}"></div>
                            </div>
                            <!--历史记录-->
                            <div v-show="!NowAndHistory">
                                <div>
                                    <a class="btn btn-link pull-right"  @click="NowAndHistory=true">查看当前数据</a>
                                </div>
                                <div id="myChartHistory" :style="{width: '600px', height: '500px',margin:'auto'}"></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            sensorInfVisible: false, //是否显示传感器图表数据
            NowAndHistory: true, //当前数据和历史记录切换
            typenum: 0,
            items: [{
                id: 1, // 设备 农场 id
                name: 1 // 设备 等 名称
            }],
            sensorInf:{
                max:0, // 当前节点种植作物环境上限
                min:0,// 当前节点种植作物环境下限
                now:0,// 当前节点当前温度
                type:"温度|气压" //传感器类型
            },
            typeCheckData: [],
            initData: [{
                    url: "sceneFarm",
                    title: "农场"
                },
                {
                    url: "greenHouse",
                    title: "大棚"
                },
                {
                    url: "equip",
                    title: "设备"
                },
                {
                    url: "sensor",
                    title: "传感器",

                },
            ],
        }
    },
    mounted() {
        this.drawChartNow();
    },
    methods: {
        loginSensorInfData(id) {
            var vm = this;
            vm.sensorInfVisible = true;
            vm.$axios.post(vm.HOST + "", {
                id: id
            }).then(success => {
                vm.sensorInfVisible = true;
                this.drawChartNow();
            }).catch(error => {})
        },
        loginItems(direction, item = "") {
            var vm = this;
            var typenum = vm.typenum;
            if (typenum == vm.initData.length - 1) {
                if (direction > 0) {
                    vm.loginSensorInfData();
                } else if (direction < 0 && vm.sensorInfVisible) {
                    vm.sensorInfVisible = false;
                    vm.NowAndHistory = true;
                    return;
                }

            }
            if (item != "") {
                vm.typeCheckData[vm.typenum] = item;
            }
            if (direction > 0) {
                vm.typenum += 1;
            } else if (direction < 0) {
                vm.typenum -= 1;
            }
            typenum = vm.typenum;
            if (typenum <= vm.initData.length - 1) {
                vm.loginThisItems(vm.initData[typenum].url, item != '' ? item.id : (vm.typenum == 0 ? "" : vm.typeCheckData[typenum].id))
            } else {
                vm.typenum = vm.initData.length - 1;
                typenum = vm.typenum;
            }
        },
        loginThisItems(url, id = "") {
            var vm = this;
            vm.$axios.post(vm.HOST + url, id != "" ? {
                id: id
            } : "").then(success => {
                vm.items = success.data;
            }).catch(error => {})
        },
        drawChartNow() {
            var vm = this;
            let myChart = vm.$echarts.init(document.getElementById('myChartNow'));
            vm.$axios.post(vm.HOST + "").then(success => {
                
            }).catch(error=>{})
            var splitNumber = 10;
                var min = 0;
                var max = 50;
                var len = max - min;
                if (len > 10 || len < 6) {
                    if (len > 10 && len < 20) {
                        splitNumber = 10;
                    }
                } else if (len <= 10 && len >= 6) {
                    splitNumber = (len) + 4;
                }
                var option = {
                    tooltip: {
                        formatter: "{a} <br/>{b} : {c} ℃"
                    },
                    toolbox: {
                        feature: {
                            restore: {},
                            saveAsImage: {}
                        }
                    },
                    splitNumber: splitNumber,
                    series: [{
                        min: min - len / (splitNumber - 4) * 2,
                        max: max + len / (splitNumber - 4) * 2,
                        splitNumber: splitNumber,
                        name: '温度',
                        type: 'gauge',
                        detail: {
                            formatter: '{value} ℃'
                        },
                        data: [{
                            value: 32.9,
                            name: '温度'
                        }],
                        markPoint: {
                            symbol: 'circle',
                            symbolSize: 5,
                            data: [
                                //跟你的仪表盘的中心位置对应上，颜色可以和画板底色一样
                                {
                                    x: 'center',
                                    y: 'center',
                                    itemStyle: {
                                        color: '#FFF'
                                    }
                                }
                            ]
                        },
                        axisLabel: {
                            show: true,
                            formatter: function (value) {
                                return value.toFixed(1);
                            },
                            textStyle: {
                                color: 'auto'
                            }
                        },
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: [
                                    [2 / splitNumber, '#ec2a08'],
                                    [(splitNumber - 2) / splitNumber, '#21f32cc4'],
                                    [1, '#ec2a08']
                                ]
                            }
                        },
                        axisTick: {
                            splitNumber: 10,
                        }
                    }],
                };
                console.log()
                setInterval(function () {
                    option.series[0].data[0].value = (Math.random() * 100).toFixed(1) - 0;
                    myChart.setOption(option, true);
                }, 2000);

        }
    }
}
</script>

<style scoped>
.logindata-error {
    font-size: 18px;
    color: rgb(243, 29, 29);
    color: #21f32cc4
}
</style>



